 <template>
    <section class="app-header">
        <div class="logo">
            PLANT SHOP</div>
        <nav>
            <router-link to="/">HOME</router-link>
            <router-link to="/stats">STATS</router-link>
            <router-link to="/admin">ADMIN</router-link>
        </nav>
        <div class="shopping-cart">
            <button class="dropbtn">Shopping Cart {{ cartProducts.length }}</button>
            <div v-if="cartProducts.length > 0" class="dropdown-content">
                <div class="cart-products" v-for="product in cartProducts" :key="product">
                    <div class="products">
                        <p>{{ product.title }}</p>
                        <p>{{ product.price }} $</p>
                    </div>
                </div>
                <div class="total">
                    <p>Total: </p>
                    <p>{{ cartTotal }} $</p>
                </div>
                <button @click="pay">Checkout</button>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: 'app-header',
    methods: {
        pay() {
            this.$store.dispatch({ type: 'pay', cart: this.cartProducts, total: this.cartTotal })
        }
    },
    computed: {
        cartProducts() {
            return this.$store.getters.cart
        },
        cartTotal() {
            return this.$store.getters.cartTotal
        },
    }
}
</script>